/* src/App.css */
.App {
  text-align: center;
  background-color: #121212;
  color: #e0e0e0;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-family: Arial, sans-serif;
}

.section {
  margin: 20px;
}

button {
  background-color: #1f1f1f;
  color: #e0e0e0;
  border: 1px solid #333;
  padding: 10px 20px;
  margin-top: 20px;
  cursor: pointer;
  font-size: 16px;
}

button:hover {
  background-color: #333;
}

